"use client";
import React, { useState } from "react";
import { useParams, useRouter } from "next/navigation";
import { useLoginClient } from "../../context/regester/login_context";
import { useApi } from "../../context/ApiContext";

export default function AddPortfolioImages() {
  const { token, saveData } = useLoginClient();
  const { baseUrl } = useApi();
  const { id } = useParams(); // id من URL
  const router = useRouter();

  const [files, setFiles] = useState<FileList | null>(null);
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!files || files.length === 0) {
      alert("من فضلك اختر صورة واحدة على الأقل.");
      return;
    }

    const formData = new FormData();
    Array.from(files).forEach((file) => {
      formData.append("ImageFiles", file);
    });

    try {
      setLoading(true);

      const res = await fetch(`${baseUrl}/api/Portfolio/${id}/images`, {
        method: "POST",
        headers: {
          Authorization: `Bearer ${token}`,
        },
        body: formData,
      });

      if (!res.ok) throw new Error("فشل في رفع الصور");

      alert("✅ تم رفع الصور بنجاح");

      // ✅ إعادة التوجيه إلى بروفايل العامل المسجل
      if (saveData?.workerId) {
        router.push(`/viewprofile/${saveData.workerId}/portfolio`);
      } else {
        router.push("/"); // fallback في حالة عدم وجود workerId
      }
    } catch (err) {
      console.error("❌ Error uploading image:", err);
      alert("حدث خطأ أثناء رفع الصور");
    } finally {
      setLoading(false);
    }
  };

  if (loading) return <div className="p-4">جارٍ رفع الصور...</div>;

  return (
    <div className="max-w-md mx-auto p-4 bg-white mt-10 rounded shadow">
      <h1 className="text-xl font-bold text-green-700 mb-4 text-center">
        إضافة صور للبورتفوليو
      </h1>

      <form onSubmit={handleSubmit} className="space-y-4">
        <input
          type="file"
          accept="image/*"
          multiple
          onChange={(e) => setFiles(e.target.files)}
          className="w-full border p-2 rounded"
          required
        />

        <div className="flex justify-between">
          <button
            type="button"
            onClick={() => router.back()}
            className="bg-gray-300 hover:bg-gray-400 text-sm px-4 py-2 rounded"
          >
            إلغاء
          </button>
          <button
            type="submit"
            className="bg-green-600 hover:bg-green-700 text-white text-sm px-4 py-2 rounded"
          >
            رفع الصور
          </button>
        </div>
      </form>
    </div>
  );
}
